      async NoDo(index) {
        if (this.owner.isownerLogin) {
          try {
            const whouser = this.whodo[index].deal[0]
            console.log(whouser)
            await this.api.patch(
              'cases/dealNO/' + this.whodo[index]._id,
              { userId: whouser },
              {
                headers: {
                  authorization: 'Bearer ' + this.owner.token
                }
              }
            )
            this.$swal({
              icon: 'success',
              title: '',
              text: ''
            })
            this.getNew()
          } catch (error) {
            console.log(error)
            this.$swal({
